function App() {
  return (
    <div>
      <div className="header">
        <div className="header__logo">
          <img height="100" width="100" src="../img/logo.png" alt="No logo" />
        </div>
        <div className="header__search">
          <input className="header__search__input" type="search" placeholder="Search books" />

          <img src="../img/outline_search.png" alt="" />
        </div>
      </div>
    </div>
  );
}

export default App;
